@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;500&display=swap");
* {
  font-family: "Montserrat";
}

::-webkit-scrollbar {
  width: 2px;
}

.App {
  text-align: center;
  color: #2A52BE;
  background-color: #FEFAFA;
}

h1, h2, h3, h4, h5, h6, p, a {
  margin: 0px;
}

a {
  text-decoration: none;
}

div div.pc-hidden,
div h3.pc-hidden {
  display: none;
}
div div.phone-hidden,
div h3.phone-hidden {
  display: none;
}

label {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 40svw;
}
label span {
  top: 10px;
  left: 10px;
  pointer-events: none;
  padding: 0px 10px;
  border-radius: 8px 8px 0px 0px;
  width: fit-content;
}
label input {
  width: calc(100% - 22px);
  height: 40px;
  border: solid 1px #BDBDBD;
  border-radius: 8px;
  padding: 2px 10px;
  font-size: 1rem;
}
label input:focus-visible {
  outline: none;
}

.button {
  width: max-content;
}
.button:hover {
  cursor: pointer;
}

.button1 p, .button1 a {
  padding: 15px;
  background-color: #2A52BE;
  color: #FFFFFF;
  border-radius: 8px;
  text-align: center;
}

.button2 p, .button2 a {
  padding: 15px;
  background-color: #FFFFFF;
  color: #2A52BE;
  border-radius: 8px;
}

.menu-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 22px;
  width: 30px;
  overflow: hidden;
  z-index: 3000000;
}
.menu-btn .line {
  width: 100%;
  border-radius: 25px;
  height: 13%;
  background-color: #2A52BE;
}

.pages {
  width: 100dvw;
  display: flex;
  color: #2D2E2E;
}

.page-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  position: absolute;
  left: 250px;
  width: calc(100dvw - 250px - 2px);
}

.app-features {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.app-features .heading {
  margin: 30px 0px 0px 0px;
  color: #2A52BE;
  font-size: 2rem;
  letter-spacing: 3px;
}

.about-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
.about-page .header-text {
  font-size: 2rem;
  text-align: center;
}
.about-page p {
  font-size: 1.5rem;
}

.dashboard-main-section {
  width: 94%;
  margin: 10px 10px 0px;
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 190px 140px 140px 80svh 30svh 40svh;
  grid-template-areas: "dashboardad dashboardad dashboardcalendar" "dashboardbox dashboardbox dashboardcalendar" "dashboardbox dashboardbox dashboardcalendar" "classandexamschedule classandexamschedule classandexamschedule" "dashboardtasks dashboardtasks dashboardgroups" "dashboardtasks dashboardtasks dashboardgroups";
}

.login-signup {
  display: grid;
  grid-template-columns: repeat(2, 50%);
  height: 100dvh;
  width: 100dvw;
}
.login-signup .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.login-signup .left h1 {
  color: #2A52BE;
}
.login-signup .left img {
  max-width: calc(100% - 60px);
  object-fit: cover;
}
.login-signup .right {
  background-color: #2A52BE;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.login-signup .right .phone-design {
  display: flex;
  flex-direction: column;
  gap: 10px;
  background-color: #FFFFFF;
  border-radius: 24px 24px 0px 0px;
  min-height: 50svh;
  width: 55%;
  padding: 7.5%;
}
.login-signup .right .phone-design .alternative {
  align-self: center;
}
.login-signup .right .phone-design .alternative a {
  color: #2A52BE;
}
.login-signup .onboarding-right {
  justify-content: center;
  color: #FEFAFA;
}
.login-signup .container {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 80%;
  height: max-content;
}
.login-signup .container .options {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: max-content;
  max-height: 205px;
  overflow-y: scroll;
  background-color: #FFFFFF;
  border-radius: 0px 0px 10px 10px;
  position: relative;
}
.login-signup .container .options .option {
  display: flex;
  align-items: center;
  max-width: calc(100% - 20px);
  min-height: 50px;
  padding: 0px 10px;
  border-top: solid 1px #2D2E2E;
  color: #2D2E2E;
}
.login-signup .remember-me-forgot-password {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 0px;
}
.login-signup .remember-me-forgot-password h5, .login-signup .remember-me-forgot-password p {
  font-size: 1rem;
}
.login-signup .remember-me-forgot-password .remember-me {
  display: flex;
  align-items: center;
}
.login-signup .or-div {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0px;
}
.login-signup .or-div .line {
  height: 0px;
  width: 100%;
  border: solid 1px #E0E0E0;
}
.login-signup .or-div .text {
  position: relative;
  top: -11px;
  background-color: #FFFFFF;
  padding: 0px 15px;
}
.login-signup .switch-screen {
  display: flex;
  align-items: center;
}
.login-signup .all {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  grid-column: span 2;
  height: 100%;
  width: 100vw;
}
.login-signup .all a {
  color: #FFFFFF;
}
.login-signup .all img {
  max-width: 100%;
}

.settings-page,
.user-profile-page {
  padding: 0px 0px 0px 20px;
  width: calc(100% - 20px);
  gap: 10px;
}

.settings-page {
  display: grid;
  grid-template-areas: "top top" "left right";
  grid-template-columns: 60% calc(40% - 10px);
  background-color: #FFFFFF;
}
.settings-page h3 {
  color: #2A52BE;
}
.settings-page .page-right-header {
  grid-area: top;
}
.settings-page .left {
  display: flex;
  flex-direction: column;
  gap: 40px;
  grid-area: left;
  align-items: flex-start;
}
.settings-page .right {
  grid-area: right;
  height: calc(100dhv - 95px);
}

.settings-section {
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-bottom: solid 1px #D1D1D1;
  padding: 20px 0px;
}
.settings-section h3 {
  padding: 0px 0px 20px 0px;
  border-bottom: solid 1px #D1D1D1;
}

input[type=file] {
  display: none;
}

.user-profile-page {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
}
.user-profile-page h3 {
  color: #2A52BE;
}
.user-profile-page .user-profile-heading {
  border-bottom: solid 1px #D1D1D1;
  padding: 10px 0px 20px 0px;
  width: 40svw;
}
.user-profile-page .upload-file {
  height: 90px;
  width: 90px;
  border: dotted;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin: 20px 0px 0px 0px;
}
.user-profile-page .upload-file::after {
  text-align: center;
  content: "Upload your photo";
  font-size: 0.8rem;
}
.user-profile-page .button {
  margin: 20px 0px 0px 0px;
}

.get-started {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  color: #2A52BE;
  border-radius: 8px;
  padding: 20px;
  width: fit-content;
  margin: 15px 0px 0px 30px;
}

.table p {
  font-size: 0.8rem;
}
.table .row {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 45px;
  border-bottom: solid 0.8px rgba(0, 24, 12, 0.2);
}
.table .table-status {
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  border-radius: 17px;
}

.top-navbar {
  background-color: #FFFFFF;
  height: 70px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.top-navbar img {
  height: 90%;
  margin: 0px 0px 0px 15px;
  z-index: 99;
}
.top-navbar .top-nav-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  margin: 0px 40px 0px 0px;
}
.top-navbar a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #2D2E2E;
  font-size: 1rem;
  padding: 10px;
}
.top-navbar .login {
  background-color: #2A52BE;
  color: #FFFFFF;
  border-radius: 8px;
}
.top-navbar .menu-btn {
  left: 20px;
}
.top-navbar .menu-btn.pc-hidden {
  display: none;
}
.top-navbar .header-side-menu {
  width: 100%;
  min-height: 100dvh;
  max-height: 100dvh;
  position: fixed;
  top: -100dvh;
  background-color: #FFFFFF;
  transition: top 0.3s ease; /* Add a transition effect for smooth sliding */
}
.top-navbar .header-side-menu .slide-menu-links {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.top-navbar .header-side-menu .slide-menu-links .space {
  display: flex;
  align-items: center;
  height: 70px;
  width: 100%;
}
.top-navbar .header-side-menu .slide-menu-links .space img {
  height: 80%;
  width: auto;
}
.top-navbar .header-side-menu .slide-menu-links a {
  color: #2A52BE;
  width: 80%;
}
.top-navbar .header-side-menu .slide-menu-links .login {
  color: #FEFAFA;
}
.top-navbar .header-side-menu.visible {
  top: 0; /* Show the side menu by setting right to 0 */
}

.landing-banner {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, 50%);
  height: calc(100vh - 70px);
  width: 100%;
  background-color: #2A52BE;
}
.landing-banner .banner-left,
.landing-banner .banner-right {
  width: 100%;
}
.landing-banner .banner-left h2, .landing-banner .banner-left p,
.landing-banner .banner-right h2,
.landing-banner .banner-right p {
  padding: 15px 30px;
  max-width: 95%;
  color: #FFFFFF;
}
.landing-banner .banner-left h2,
.landing-banner .banner-right h2 {
  font-size: 2rem;
}
.landing-banner .banner-left p,
.landing-banner .banner-right p {
  font-size: 1.5rem;
}
.landing-banner .banner-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100vh - 70px);
}
.landing-banner .banner-right.pc-hidden {
  display: none;
}
.landing-banner .banner-right {
  justify-content: center;
  align-items: end;
  display: grid;
  grid-template-areas: "studentimage1 studentimage2" "studentimage3 studentimage3";
  grid-template-rows: 43% 55%;
  grid-template-columns: repeat(2, 46.3%);
  gap: 10px;
  height: calc(100vh - 70px - 40px);
}
.landing-banner .banner-right .student-image-1 {
  background-color: #F05454;
  border-radius: 46px 0px;
  grid-area: studentimage1;
  background-image: url(./Images/Student1.png);
}
.landing-banner .banner-right .student-image-2 {
  background-color: #CDF;
  border-radius: 0px 46px;
  grid-area: studentimage2;
  background-image: url(./Images/Student2.png);
}
.landing-banner .banner-right .student-image-3-div {
  grid-area: studentimage3;
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  justify-self: center;
}
.landing-banner .banner-right .student-image-3-div .students-adorn-1 {
  transform: rotate(358deg);
}
.landing-banner .banner-right .student-image-3-div .students-adorn-2 {
  transform: rotate(272deg);
}
.landing-banner .banner-right .student-image-3 {
  background-color: #6ED097;
  border-radius: 40px 40px 0px 0px;
  background-image: url(./Images/Student3.png);
  height: 100%;
  width: 57%;
}
.landing-banner .banner-right .student-image-1,
.landing-banner .banner-right .student-image-2 {
  height: 100%;
  width: 100%;
}
.landing-banner .banner-right .student-image-1,
.landing-banner .banner-right .student-image-2,
.landing-banner .banner-right .student-image-3 {
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
}

.about-banner {
  display: grid;
  grid-template-columns: 6fr 5fr;
  grid-template-rows: 1fr;
  gap: 30px;
  padding: 30px;
  width: calc(100dvw - 60px);
  height: 47vh;
  background: rgba(204, 221, 255, 0.5);
}
.about-banner .banner-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  gap: 10px;
}
.about-banner .banner-left p {
  font-size: 1.4rem;
}
.about-banner .banner-left img {
  width: 62%;
  object-fit: cover;
}
.about-banner .banner-right {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.about-banner .banner-right img {
  width: 100%;
  object-fit: cover;
}

.our-mission {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% - 60px);
  padding: 15px 30px 55px 30px;
  background: url(./Images/aboutpage/OurMissionAdorn.png) left bottom/8% no-repeat;
}
.our-mission h1 {
  margin: 0px 0px 15px 0px;
}

.about-edvantage {
  display: grid;
  grid-template-areas: "whowearedescrip whoweareimage" "whatdrivesusimage whatdrivesusdescrip";
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  width: calc(100dvw - 60px);
}
.about-edvantage img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.about-edvantage .who-we-are-descrip,
.about-edvantage .who-we-are-image,
.about-edvantage .what-drives-us-descrip,
.about-edvantage .what-drives-us-image {
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}
.about-edvantage .who-we-are-descrip {
  grid-area: whowearedescrip;
}
.about-edvantage .who-we-are-image {
  grid-area: whoweareimage;
}
.about-edvantage .what-drives-us-descrip {
  grid-area: whatdrivesusdescrip;
}
.about-edvantage .what-drives-us-image {
  grid-area: whatdrivesusimage;
}

.key-features {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100dvw - 60px);
  gap: 30px;
  margin: 0px 0px 20px 0px;
}
.key-features .features {
  display: grid;
  width: 100%;
  grid-template-areas: "feature1 feature2" "feature3 feature4";
  gap: 30px;
  align-items: center;
}
.key-features .features .feature-1,
.key-features .features .feature-2,
.key-features .features .feature-3,
.key-features .features .feature-4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  height: calc(100% - 40px);
  width: calc(100% - 40px);
  padding: 20px;
  border: solid 1px;
  border-radius: 8px;
}
.key-features .features .feature-1 h1,
.key-features .features .feature-2 h1,
.key-features .features .feature-3 h1,
.key-features .features .feature-4 h1 {
  font-size: 1.3rem;
}
.key-features .features .feature-1 {
  grid-area: feature1;
  border-color: #063;
}
.key-features .features .feature-2 {
  grid-area: feature2;
  border-color: #4A3AFF;
}
.key-features .features .feature-3 {
  grid-area: feature3;
  border-color: #F9E109;
}
.key-features .features .feature-4 {
  grid-area: feature4;
  border-color: #D32F2F;
}

.work-with-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  width: calc(100dvw - 60px);
}
.work-with-us .card {
  display: grid;
  grid-template-areas: "workwithusdescrip workwithusimage";
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  width: 100%;
  padding: 0px 0px 20px 0px;
}
.work-with-us .card .work-with-us-descrip,
.work-with-us .card .work-with-us-image {
  max-height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}
.work-with-us .card .work-with-us-descrip img,
.work-with-us .card .work-with-us-image img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}
.work-with-us .card .work-with-us-descrip {
  grid-area: workwithusdescrip;
}
.work-with-us .card .work-with-us-image {
  grid-area: workwithusimage;
}

.app-feature {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  min-height: 25dvh;
  width: 100%;
  gap: 20px;
  margin: 10px 0px 10px 0px;
  width: calc(100% - 60px);
}
.app-feature .left {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
}
.app-feature .left .feature-summary {
  grid-column: span 2;
}
.app-feature .left .feature-summary,
.app-feature .left .feature-1,
.app-feature .left .feature-2 {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.app-feature .left .feature-summary h1,
.app-feature .left .feature-1 h1,
.app-feature .left .feature-2 h1 {
  font-size: 1.5rem;
}
.app-feature .right {
  width: 100%;
  height: 40dvh;
}
.app-feature .right img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.app-feature-gamification {
  grid-template-columns: repeat(2, 46%);
  justify-content: center;
  background-color: #2A52BE;
  color: #FFFFFF;
  background-image: url(./Images/landingpage/GamificationAdorn.png);
  background-repeat: no-repeat;
  background-size: 10%;
  width: 100%;
}
.app-feature-gamification .gamification-header {
  grid-column: span 2;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  letter-spacing: 2px;
  height: 70px;
}
.app-feature-gamification .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}
.app-feature-gamification .left .large-text {
  font-size: 2rem;
}
.app-feature-gamification .left p {
  font-size: 1rem;
}
.app-feature-gamification .left .gamification-signup {
  display: flex;
  align-items: center;
  gap: 10px;
}
.app-feature-gamification .left .get-started {
  margin: 0px;
}
.app-feature-gamification .left .no-button {
  background: none;
  color: #FEFAFA;
}
.app-feature-gamification .right {
  height: 100%;
}
.app-feature-gamification .right img {
  max-height: 40dvh;
  width: 100%;
  object-fit: cover;
}

.landing-testimonial {
  display: grid;
  width: 100%;
  min-height: 50vh;
  margin: 0px 0px 20px 0px;
  grid-template-areas: "testimonialheader testimonialheader" "testimonialleft testimonialright";
  grid-template-columns: 17% 80%;
  grid-template-rows: 70px auto;
  gap: 3%;
}
.landing-testimonial .testimonial-header {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: testimonialheader;
  height: 70px;
  color: #2A52BE;
}
.landing-testimonial .testimonial-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-area: testimonialleft;
  background-image: url(./Images/landingpage/TestimonialLeftAdorn.png);
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
}
.landing-testimonial .testimonial-right {
  grid-area: testimonialright;
  background-image: url(./Images/landingpage/TestimonialRightAdorn.png);
  background-size: 100%;
  width: 100%;
}

.footer {
  min-height: 50dvh;
  width: 100%;
  background: #2A52BE;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 30px 0px;
  color: #FFFFFF;
}
.footer .footer-contact-icons {
  display: flex;
  align-items: center;
  gap: 10px;
}

.side-menu {
  width: 250px;
  height: 100dvh;
  background: #2A52BE;
  position: fixed;
}
.side-menu .side-menu-logo {
  display: block;
}
.side-menu nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  position: relative;
  top: 60px;
  max-height: calc(100dvh - 136px);
