// Import
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;500&display=swap');

// Variables
$primary-color: #2A52BE;
$primary-color2: #FEFAFA;
$text-color: #2D2E2E;
$white: #FFFFFF;
$top-navbar-height: 70px;
$button-radius: 8px;
$landingbannerheight: calc(100vh - $top-navbar-height);
$side-menu-width: 250px;
$chat-left-width: 310px;
$page-right-header-height: 55px;

@mixin fdja {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
};
@mixin fdj {
    display: flex;
    flex-direction: column;
    justify-content: center;
};
@mixin fda {
    display: flex;
    flex-direction: column;
    align-items: center
};
@mixin fja {
    display: flex;
    justify-content: center;
    align-items: center;
};
@mixin fj {
    display: flex;
    justify-content: center;
};
@mixin fa {
    display: flex;
    align-items: center
};
@mixin fd{
    display: flex;
    flex-direction: column;
}

// General
*{
    font-family: 'Montserrat';
}
::-webkit-scrollbar{
    width: 2px;
}
.App {
    text-align: center;
    color: $primary-color;
    background-color: $primary-color2;
}
h1, h2, h3, h4, h5, h6, p, a{
    margin: 0px;
}
a{
    text-decoration: none;
}
div{
    div.pc-hidden,
    h3.pc-hidden{
        display: none
    }
    div.phone-hidden,
    h3.phone-hidden{
        display: none
    }
}

// Input fields
label{
    @include fd();
    gap: 10px;
    max-width: 40svw;
    // position: relative;
    span{
        top: 10px;
        left: 10px;
        pointer-events: none;
        padding: 0px 10px;
        border-radius: 8px 8px 0px 0px;
        width: fit-content;
    }
    input{
        width: calc(100% - 22px);
        height: 40px;
        border: solid 1px #BDBDBD;
        border-radius: 8px;
        padding: 2px 10px;
        font-size: 1rem;
        &:focus-visible{
            outline: none;
        }
    }
}
.button{
    width: max-content;
    &:hover{
        cursor: pointer;
    }
}
.button1{
    p,a{
        padding: 15px;
        background-color: $primary-color;
        color: $white;
        border-radius: 8px;
        text-align: center;
    }
}
.button2{
    p,a{
        padding: 15px;
        background-color: $white;
        color: $primary-color;
        border-radius: 8px;
    }
}
// General - Menu Button
.menu-btn{
    @include fda;
    justify-content: space-around;
    height: 22px;
    width: 30px;
    overflow: hidden;
    .line{
        width: 100%;
        border-radius: 25px;
        height: 13%;
        background-color: $primary-color;
    }
    z-index: 3000000;
}

// Pages
.pages{
    width: 100dvw;
    display: flex;
    color: $text-color;
}
  .page-right{
    @include fda;
    gap: 10px;
    position: absolute;
    left: 250px;
    width: calc(100dvw - $side-menu-width - 2px);
}

// Pages - Landing Page
.app-features{
    @include fda();
    .heading{
        margin: 30px 0px 0px 0px;
        color: $primary-color;
        font-size: 2rem;
        letter-spacing: 3px;
    }
}
// Pages - About Page
.about-page{
    @include fda();
    gap: 10px;
    .header-text{
        font-size: 2rem;
        text-align: center;
    }
    p{
        font-size: 1.5rem;
    }
}

// Pages - Dashboard
.dashboard-main-section{
    width: 94%;
    margin: 10px 10px 0px;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 190px 140px 140px 80svh 30svh 40svh;
    grid-template-areas: 
        "dashboardad dashboardad dashboardcalendar"
        "dashboardbox dashboardbox dashboardcalendar"
        "dashboardbox dashboardbox dashboardcalendar"
        "classandexamschedule classandexamschedule classandexamschedule"
        "dashboardtasks dashboardtasks dashboardgroups"
        "dashboardtasks dashboardtasks dashboardgroups";
}

// Pages - Login
.login-signup{
    display: grid;
    grid-template-columns: repeat(2, 50%);
    height: 100dvh;
    width: 100dvw;
    .left{
        @include fdja();
        gap: 10px;
        h1{
            color: $primary-color;
        }
        img{
            max-width: calc(100% - 60px);
            object-fit: cover;
        }
    }
    .right{
        background-color: $primary-color;
        @include fda();
        justify-content: flex-end;
        .phone-design{
            @include fd();
            gap: 10px;
            background-color: $white;
            border-radius: 24px 24px 0px 0px;
            min-height: 50svh;
            width: 55%;
            padding: calc(15% / 2);
            .alternative{
                align-self: center;
                a{
                    color: $primary-color;
                }
            }
        }
    }
    .onboarding-right{
        justify-content: center;
        color: $primary-color2;
    }
    .container{
        position: relative;
        @include fd;
        gap: 30px;
        width: 80%;
        height: max-content;
        .options{
            @include fd;
            width: 100%;
            height: max-content;
            max-height: 205px;
            overflow-y: scroll;
            background-color: $white;
            border-radius: 0px 0px 10px 10px;
            position: relative;
            .option{
                @include fa;
                max-width: calc(100% - 20px);
                min-height: 50px;
                padding: 0px 10px;
                border-top: solid 1px $text-color;
                color: $text-color
            }
        }
    }
    .remember-me-forgot-password{
        @include fa();
        justify-content: space-between;
        padding: 15px 0px;
        h5, p{
            font-size: 1rem
        }
        .remember-me{
            @include fa();
        }
    }
    .or-div{
        width: 100%;
        @include fda();
        padding: 20px 0px;
        .line{
            height: 0px;
            width: 100%;
            border: solid 1px #E0E0E0;
            // position: relative;
            // top: 0px;
        }
        .text{
            position: relative;
            top: -11px;
            background-color: $white;
            padding: 0px 15px;
        }
    }
    .switch-screen{
        @include fa;
    }
    .all{
        @include fdja;
        gap: 20px;
        grid-column: span 2;
        height: 100%;
        width: 100vw;
        a{
            color: $white;
        }
        img{
            max-width: 100%;
        }
    }
}

.settings-page,
.user-profile-page{
    padding: 0px 0px 0px 20px;
    width: calc(100% - 20px);
    gap: 10px;
}

// Pages - Settings Page
.settings-page{
    display: grid;
    grid-template-areas: 
    "top top"
    "left right";
    grid-template-columns: 60% calc(40% - 10px);
    background-color: $white;
    h3{
        color: $primary-color;
    }
    .page-right-header{
        grid-area: top;
    }
    .left{
        @include fd();
        gap: 40px;
        grid-area: left;
        align-items: flex-start;
    }
    .right{
        grid-area: right;
        height: calc(100dhv - 95px)
    }
}
// Pages - Password Reset
.settings-section{
    @include fd();
    gap: 10px;
    border-bottom: solid 1px #D1D1D1;
    padding: 20px 0px;
    h3{
        padding: 0px 0px 20px 0px;
        border-bottom: solid 1px #D1D1D1;
    }
}


// Pages - User Profile Page
input[type="file"]{
    display: none;
}
.user-profile-page{
    background-color: $white;
    @include fd();
    h3{
        color: $primary-color;
    }
    .user-profile-heading{
        border-bottom: solid 1px #D1D1D1;
        padding: 10px 0px 20px 0px;
        // color: $primary-color;
        width: 40svw;
    }
    // input{
    //     // background-color: #4891D30D;
    // }
    .upload-file{
        height: 90px;
        width: 90px;
        border: dotted;
        border-radius: 8px;
        @include fdja();
        gap: 10px;
        // background-color: #4891D30D;
        margin: 20px 0px 0px 0px;
        &::after{
            text-align: center;
            content: "Upload your photo";
            font-size: 0.8rem;
        }
    }
    .button{
        margin: 20px 0px 0px 0px;
    }
}



// Components
// Get Started button
.get-started{
    @include fja();
    background-color: $white;
    color: $primary-color;
    border-radius: $button-radius;
    padding: 20px;
    width: fit-content;
    margin: 15px 0px 0px 30px;
}
.table{
    p{
        font-size: 0.8rem;
    }
    .row{
        @include fa();
        width: 100%;
        min-height: 45px;
        border-bottom: solid .8px rgba(0, 24, 12, 0.2);
    }
    // .column{
    //     @include fa();
    //     height: 100%;
    // }
    .table-status{
        width: fit-content;
        @include fja();
        padding: 5px 10px;
        border-radius: 17px;
    }
}

// Top Navbar
.top-navbar{
    background-color: $white;
    height: $top-navbar-height;
    width: 100%;
    @include fa();
    justify-content: space-between;
    img{
        height: 90%;
        margin: 0px 0px 0px 15px;
        z-index: 99;
    }
    .top-nav-links{
        @include fa();
        justify-content: space-between;
        gap: 30px;
        margin: 0px 40px 0px 0px;
    }
    a{
        @include fja();
        color: $text-color;
        font-size: 1rem;
        padding: 10px;
    }
    .login{
        background-color: $primary-color;
        color: $white;
        border-radius: $button-radius;
    }
    .menu-btn{
        left: 20px;
    }
    .menu-btn.pc-hidden{
        display: none;
    }
    .header-side-menu {
        width: 100%;
        min-height: 100dvh;
        max-height: 100dvh;
        position: fixed;
        // overflow: hidden;
        top: calc(-100dvh);
        background-color: $white;
        transition: top 0.3s ease; /* Add a transition effect for smooth sliding */
        .slide-menu-links{
            @include fda;
            .space{
                @include fa;
                height: $top-navbar-height;
                width: 100%;
                img{
                    height: 80%;
                    width: auto;
                }
            }
            a{
                color: $primary-color;
                width: 80%;
            }
            .login{
                color: $primary-color2;
            }
        }
    }
    
    .header-side-menu.visible {
    top: 0; /* Show the side menu by setting right to 0 */
    }
}

// Landing Page Banner
.landing-banner{
    display: grid;
    align-items: center;
    grid-template-columns: repeat(2, 50%);
    height: $landingbannerheight;
    width: 100%;
    background-color: $primary-color;
    .banner-left,
    .banner-right{
        width: 100%;
        h2, p{
            padding: 15px 30px;
            max-width: 95%;
            color: $white;
        }
        h2{
            font-size: 2rem;
        }
        p{
            font-size: 1.5rem;
        }
    }
    .banner-left{
        @include fdj();
        height: $landingbannerheight;
    }
    .banner-right.pc-hidden{
        display: none;
    }
    .banner-right{
        justify-content: center;
        align-items: end;
        display: grid;
        grid-template-areas: 
        "studentimage1 studentimage2"
        "studentimage3 studentimage3";
        grid-template-rows: 43% 55%;
        grid-template-columns: repeat(2, 46.3%);
        gap: 10px;
        height: calc($landingbannerheight - 40px);
        .student-image-1{
            background-color: #F05454;
            border-radius: 46px 0px;
            grid-area: studentimage1;
            background-image: url(./Images/Student1.png);
        }
        .student-image-2{
            background-color: #CDF;
            border-radius: 0px 46px;
            grid-area: studentimage2;
            background-image: url(./Images/Student2.png);
        }
        .student-image-3-div{
            grid-area: studentimage3;
            @include fj;
            height: 100%;
            width: 100%;
            justify-self: center;
            .students-adorn-1{
                transform: rotate(358deg);
            }
            .students-adorn-2{
                transform: rotate(272deg);
            }
        }
        .student-image-3{
            background-color: #6ED097;
            border-radius: 40px 40px 0px 0px;
            background-image: url(./Images/Student3.png);
            height: 100%;
            width: 57%;
        }
        .student-image-1,
        .student-image-2{
            height: 100%;
            width: 100%;
        }
        .student-image-1,
        .student-image-2,
        .student-image-3{
            background-position: center top;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}

// Pages - About page
.about-banner{
    display: grid;
    grid-template-columns: 6fr 5fr;
    grid-template-rows: 1fr;
    gap: 30px;
    padding: 30px;
    width: calc(100dvw - 60px);
    height: 47vh;
    background: rgba(204, 221, 255, 0.50);
    .banner-left{
        @include fdj();
        width: 100%;
        height: 100%;
        gap: 10px;
        p{
            font-size: 1.4rem;
        }
        img{
            width: 62%;
            object-fit: cover
        }
    }
    .banner-right{
        @include fja();
        width: 100%;
        height: 100%;
        img{
            width: 100%;
            object-fit: cover;
        }
    }
}
.our-mission{
    @include fda();
    width: calc(100% - 60px);
    padding: 15px 30px 55px 30px;
    background: url(./Images/aboutpage/OurMissionAdorn.png) left bottom / 8% no-repeat;
    h1{
        margin: 0px 0px 15px 0px;
    }
}
.about-edvantage{
    display: grid;
    grid-template-areas: 
    "whowearedescrip whoweareimage"
    "whatdrivesusimage whatdrivesusdescrip";
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    width: calc(100dvw - 60px);
    img{
        object-fit: cover;
        height: 100%;
        width: 100%;
    }
    .who-we-are-descrip,
    .who-we-are-image,
    .what-drives-us-descrip,
    .what-drives-us-image{
        min-height: 30vh;
        @include fdj();
        gap: 20px;
    }
    .who-we-are-descrip{
        grid-area: whowearedescrip;
    }
    .who-we-are-image{
        grid-area: whoweareimage;
    }
    .what-drives-us-descrip{
        grid-area: whatdrivesusdescrip;
    }
    .what-drives-us-image{
        grid-area: whatdrivesusimage;
    }
}
.key-features{
    @include fda();
    width: calc(100dvw - 60px);
    gap: 30px;
    margin: 0px 0px 20px 0px;
    .features{
        display: grid;
        width: 100%;
        grid-template-areas:
        "feature1 feature2"
        "feature3 feature4";
        gap: 30px;
        align-items: center;
        .feature-1,
        .feature-2,
        .feature-3,
        .feature-4{
            @include fdj();
            gap: 10px;
            height: calc(100% - 40px);
            width: calc(100% - 40px);
            padding: 20px;
            border: solid 1px;
            border-radius: 8px;
            h1{
                font-size: 1.3rem;
            }
        }
        .feature-1{
            grid-area: feature1;
            border-color: #063;
        }
        .feature-2{
            grid-area: feature2;
            border-color: #4A3AFF;
        }
        .feature-3{
            grid-area: feature3;
            border-color: #F9E109;
        }
        .feature-4{
            grid-area: feature4;
            border-color: #D32F2F;
        }
    }
}
.work-with-us{
    @include fda();
    gap: 30px;
    width: calc(100dvw - 60px);
    .card{
        display: grid;
        grid-template-areas: 
        "workwithusdescrip workwithusimage";
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
        width: 100%;
        padding: 0px 0px 20px 0px;
        .work-with-us-descrip,
        .work-with-us-image{
            max-height: 40vh;
            @include fdj();
            gap: 20px;
            img{
                object-fit: cover;
                height: 100%;
                width: 100%;
            }
        }
        .work-with-us-descrip{
            grid-area: workwithusdescrip;
        }
        .work-with-us-image{
            grid-area: workwithusimage;
        }
    }
}

// Components - App Features
// Library
.app-feature{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 25dvh;
    width: 100%;
    gap: 20px;
    margin: 10px 0px 10px 0px;
    width: calc(100% - 60px);
    .left{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
        .feature-summary{
            grid-column: span 2;
        }
        .feature-summary,
        .feature-1,
        .feature-2{
            @include fd();
            gap: 15px;
            h1{
                font-size: 1.5rem;
            }
        }
    }
    .right{
        width: 100%;
        height: 40dvh;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            border-radius: $button-radius;
        }
    }
}
.app-feature-gamification{
    grid-template-columns: repeat(2, 46%);
    justify-content: center;
    background-color: $primary-color;
    color: $white;
    background-image: url(./Images/landingpage/GamificationAdorn.png);
    background-repeat: no-repeat;
    background-size: 10%;
    width: 100%;
    .gamification-header{
        grid-column: span 2;
        @include fja;
        font-size: 1.4rem;
        letter-spacing: 2px;
        height: $top-navbar-height;
    }
    .left{
        @include fdj();
        gap: 20px;
        .large-text{
            font-size: 2rem;
        }
        p{
            font-size: 1rem
        }
        .gamification-signup{
            @include fa();
            gap: 10px;
        }
        .get-started{
            margin: 0px;
        }
        .no-button{
            background: none;
            color: $primary-color2;
        }
    }
    .right{
        height: 100%;
        img{
            max-height: 40dvh;
            width: 100%;
            object-fit: cover;
        }
    }
}
.landing-testimonial{
    display: grid;
    width: 100%;
    min-height: 50vh;
    margin: 0px 0px 20px 0px;
    grid-template-areas: 
    "testimonialheader testimonialheader"
    "testimonialleft testimonialright";
    grid-template-columns: 17% 80%;
    grid-template-rows: $top-navbar-height auto;
    gap: 3%;
    .testimonial-header{
        @include fja();
        grid-area: testimonialheader;
        height: $top-navbar-height;
        color: $primary-color;
    }
    .testimonial-left{
        @include fdja();
        grid-area: testimonialleft;
        background-image: url(./Images/landingpage/TestimonialLeftAdorn.png);
        background-repeat: no-repeat;
        background-size: 100%;
        width: 100%;
    }
    .testimonial-right{
        grid-area: testimonialright;
        background-image: url(./Images/landingpage/TestimonialRightAdorn.png);
        background-size: 100%;
        width: 100%;
    }
}
// Footer
.footer{
    min-height: 50dvh;
    width: 100%;
    background: $primary-color;
    @include fdja();
    gap: 20px;
    padding: 30px 0px;
    color: $white;
    .footer-contact-icons{
        @include fa();
        gap: 10px;
    }
}


// Side Menu
.side-menu{
    width: $side-menu-width;
    height: 100dvh;
    background: $primary-color;
    position: fixed;
    .side-menu-logo{
        display: block;
    }
    nav{
        @include fda;
        gap: 10px;
        position: relative;
        top: 60px;
        max-height: calc(100dvh - 136px)
    }
    .nav-links{
    @include fa;
    gap: 8px;
    height: 40px;
    width: 80%;
    border-radius: $button-radius;
    color: $primary-color2;
    position: relative;
    left: 0px;
    font-size: 1rem;
    &:hover{
        justify-content: center;
        background-color: $primary-color2;
        color: $primary-color;
        .side-menu-icon{
            fill: $primary-color2;
            stroke: $primary-color;
        }
    }
    .side-menu-icon{
        height: 24px;
        width: 24px;
        stroke: white;
        fill: $primary-color;
    }
}
    .side-menu-logout{
        @include fja();
        height: 40px;
        width: 80%;
        background-color: $primary-color;
        box-shadow: 0px 0px 0px 18px $primary-color;
        position: absolute;
        bottom: 10px;
        color: $primary-color2;
        border: solid 1.2px $primary-color2;
        border-radius: 4px;
        left: calc(10%);
    }
}

// Page Right
.page-right-header{
    @include fda();
    position: sticky;
    width: calc(100% - 10px);
    background-color: #FFFFFF;
    padding: 0px 5px;
    // overflow: hidden; //To be removed
    div{
        @include fa();
    }
    .menu-btn{
        left: 20px;
        .line{
            background-color: $text-color
        }
    }
    .page-right-header-a{
    @include fa();
    width: 100%;
    gap: 20px;
    position: relative;
    height: $page-right-header-height;
    justify-content: flex-end;
        .profile-image-cont{
            height: 40px;
            width: 40px;
            img{
                border-radius: 50%;
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }
    }
    .header-side-menu {
        @include fda;
        width: 100%;
        height: 100dvh;
        position: fixed;
        // overflow: hidden;
        top: 0px;
        left: calc(-100dvw);
        background-color: $white;
        transition: left 0.3s ease;
        z-index: 2000000;
        .slide-menu-links{
            @include fd;
            width: 100%;
            gap: 20px;
            .space{
                height: $page-right-header-height;
                width: 100%;
                // background-color: $primary-color
            }
            .nav-links{
                @include fa;
                gap: 20px;
                height: max-content;
                margin: 0px 0px 0px 20px;
            }
            svg{
                height: 24px;
                width: 24px;
                stroke: $primary-color;
                fill: $white;
            }
            a{
                color: $primary-color;
                width: 80%;
            }
            .login{
                color: $primary-color2;
            }
        }
    }
    .header-side-menu.visible {
    left: 0; /* Show the side menu by setting right to 0 */
    }
    .page-right-header-b{
        @include fa();
        justify-content: space-between;
        position: relative;
        width: 100%;
        height: $page-right-header-height;
        margin: 0px 0px 10px 0px;
    }
    .progress-status{
        @include fa();
        gap: 10px;
        height: 30px;
    }
    .progress-div{
        @include fa();
        gap: 6px;
        width: fit-content;
        height: calc(98% - 16px);
        padding: 8px;
        border: solid 1px;
        border-radius: 4px;
        img{
            height: 100%;
        }
        &:hover{
            cursor: pointer;
        }
    }
    .header-popup-notification{
        @include fda;
        position: fixed;
        top: $top-navbar-height;
        right: $top-navbar-height;
        height: auto;
        min-height: 300px;
        width: 370px;
        border-radius: 5px;
        box-shadow: 0px 0px 12px #000000;
        background-color: $primary-color2;
        padding: 20px;
        gap: 20px;
        z-index: 999;
        .header-notification{
            gap: 20px;
            align-items: flex-start;
            .column1{
                width: 60px;
                height: 60px;
            }
            .column2{
                flex-direction: column;
                align-items: flex-start;
                .time{
                    color: rgba(65, 71, 94, .6);
                }
            }
        }
    }
}

// Dashboard Task Overview
.dashboard-task-overview-table{
    @include fda();
    grid-area: dashboardtasks;
    max-height: 100%;
    overflow-x: scroll;
}
.dashboard-t-o-t-header{
    background-color: #ECF7F2;
}
.column1{
  width: 37%;
}
.column2{
  width: 20%;
}
.column3{
  width: 20%;
}
.column4{
  width: 23%;
}

// Dashboard Banner
.dashboard-ad-background{
    grid-area: dashboardad;
    display: flex;
    align-items: center !important;
    // position: relative;
    width: 100%;
    height: 190px;
    background: linear-gradient(129deg, $primary-color 62.64%, rgba(42, 82, 190, 0.00) 62.65%, rgba(42, 82, 190, 0.00) 62.65%);
    .dashboard-ad-text{
        @include fdj();
        gap: 10px;
        margin: 0px 0px 0px 25px;
        color: $primary-color2;
        p{
            font-size: 0.8rem;
        }
    }
    img{
        // position: absolute;
        // right: 2px;
        // bottom: 2px;
        height: 90%;
    }
}

// Dashboard Boxes
.dashboard-boxes{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    grid-area: dashboardbox;
}
.dashboard-box{
    // position: relative;
    border-radius: 20px;
    width: 100%;
    height: 140px;
    background-color: green;
}
.boxa{
    background: rgba(152, 251, 152, 0.30);
}
.boxb{
    background: rgba(255, 215, 0, 0.30);
}
.boxc{
    background: rgba(255, 107, 107, 0.30);
}
.boxd{
    background: rgba(204, 204, 204, 0.30);
}

// Dashboard Calendar
.dashboard-calendar {
    @include fda();
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: white;
    grid-area: dashboardcalendar;
    font-size: 0.7rem;
    .header {
        @include fja();
        padding: 10px;
        button {
            background: none;
            border: none;
            cursor: pointer;
            font-size: 18px;
        }
    }
    .days-of-week {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        width: 85%;
        border-top: solid 1px rgb(0, 24, 12, 0.2);
        div {
            padding: 10px 0px;
            text-align: center;
        }
    }
    .days {
        display: grid;
        align-items: center;
        justify-items: center;
        width: 85%;
        margin: 0px 0px 10px 0px;
        grid-template-columns: repeat(7, 1fr);
    }
    .day, .empty-day {
        @include fja();
        height: 35px;
        width: 25px;
        text-align: center;
        cursor: pointer;
    }
        .day:hover {
        background-color: #e0e0e0;
        border-radius: 20px;
    }
    .empty-day {
        color: #ccc;
    }
        .current-day{
        @include fja();
        background-color: $primary-color;
        color: #FFFFFF;
        border-radius: 20px;
        height: 35px;
        width: 25px;
        margin: 0px;
    }
    .todays-tasks{
        @include fd();
        gap: 10px;
        width: 90%;
        border-top: solid 1px rgb(0, 24, 12, 0.2);
        padding: 10px 0px 0px 0px;
        h2, h3{
            font-weight: 500;
        }
        .dashboard-cal-task{
            @include fd();
            gap: 5px;
        }
        .cal-task-time{
            @include fa();
        }
    }
}
// Dashboard Groups
.dashboard-groups{
    @include fd();
    grid-area: dashboardgroups;
    background-color: $white;
    width: 100%;
    max-height: 100%;
    padding: 0px 0px 0px 10px;
    overflow-x: scroll;
    h1{
        margin: 10px 0px;
    }
    p{
        font-size: .8rem;
    }
    .dashboard-group{
        display: grid;
        grid-template-columns: 30px 1fr;
        gap: 10px;
        width: 90%;
    }
    .dashboard-group-left{
        @include fda();
        width: 100%;
        height: 100%;
        .circle{
            width: 15px;
            height: 15px;
            border-radius: 15px;
            border: solid 2px $primary-color;
        }
        .line{
            width: 0px;
            height: calc(100% - 20px);
            border: solid .5px #333;
        }
    }
    .dashboard-group-right{
        @include fdj();
        gap: 10px;
        width: 100%;
        padding: 0px 0px 15px 0px;
    }
    .group-links{
        @include fa();
        gap: 10px;
    }
    .view-resources{
        @include fja();
        border-radius: 8px;
        color: $primary-color;
        min-width: 60px;
    }
    .button p{
        padding: 8px 10px;
    }
}

// Assignment
.assignments-page{
    background-color: $white;
}
.assignment-task-overview-table{
    @include fda();
    width: 100%;
    .assignment-header,
    .assignment-table-task{
        @include fa();
        width: calc(100% - 40px);
    }
    .column1{
        width: 25%;
    }
    .column2{
        width: 20%;
    }
    .column3{
        width: 20%;
    }
    .column4{
        width: 15%;
    }
    .column5{
        width: 20%;
    }
}

// Chat
.chat-page{
    background-color: white;
    height: 100%;
    gap: 0px;
    overflow: hidden;
}
.whole-chat-section{
    top: 0px;
    display: grid;
    grid-template-columns: $chat-left-width auto;
    height: calc(100dvh - 110px);
    width: 100%;
    // height: 100%;
    border-top: solid 1px rgb(40, 40, 40, .5);
    // background-color: rebeccapurple;
    // overflow: hidden;
    .add-new-chat-profile{
        @include fd;
        gap: 20px;
        position: fixed;
        align-self: center;
        justify-self: center;
        height: 400px;
        width: 400px;
        background-color: $white;
        border-radius: 20px;
        box-shadow: 0px 0px 100vw rgb(40, 40, 40, .5);
        padding: 35px 15px 15px;
        .action-div{
            @include fa;
        }
        label{
            gap: 10px;
        }
        .long-label{
            input{
                height: 70px;
            }
        }
    }
}
.left,
.right{
    margin-top: 15px;
    height: calc(100% - 15px);
}
.left{
    @include fda;
    border-right: solid 1px rgb(40, 40, 40, .5);
    // height: 100%;
    overflow: hidden;
    .chat-classification{
        @include fa;
        justify-content: space-evenly;
        width: 100%;
        .classification{
            padding: 6px 11px;
            border-radius: 20px;
            background-color: rgb(204, 204, 204, .2);
            &:hover{
                cursor: pointer;
            }
        }
    }
    .chat-profiles{
        @include fd;
        gap: 10px;
        overflow-x: scroll;
        height: calc(100% - 66px - 60px);
        width: 100%;
        border-bottom: solid 1px rgb(40, 40, 40, .5);
        margin-top: 20px;
        &::-webkit-scrollbar {
            display: none;
        }
    }
    .chat-profile{
        @include fa;
        gap: 20px;
        margin: 0px 10px 0px 10px;
        height: 70px;
        &:hover{
            cursor: pointer;
        }
        .column1,
        .column2{
            margin-top: 10px;
        }
        .column1{
            width: 50px;
            height: 50px;
            border-radius: 25px;
            img{
                height: 100%;
                width: 100%;
                border-radius: 25px;
                object-fit: cover;
            }
        }
        .column2{
            width: 100%;
        }
    }
    .left-bottom{
        @include fa;
        height: 60px;
        width: 100%;
    }
    .add-chat-icon{
        @include fja;
        height: 45px;
        width: 45px;
        border-radius: 50%;
        background-color: $primary-color;
        position: absolute;
        left: 15px;
    }
    .cross-vert-line{
        position: absolute;
        width: 55%;
        height: 7%;
        border-radius: 20px;
        background-color: $primary-color2;
    }
    .cross-horiz-line{
        position: absolute;
        width: 7%;
        height: 55%;
        border-radius: 20px;
        background-color: $primary-color2;
    }
}
.right{
    @include fda;
    width: 100%;
    height: calc(100% - 15px);
    overflow: hidden;
    .chat-header{
        @include fa;
        width: 100%;
        height: 60px;
        svg{
            display: none;
        }
        img{
            height: 50px;
            width: 50px;
            object-fit: cover;
            border-radius: 50%;
            margin: 0px 0px 0px 15px;
        }
        h3{
            margin: 0px 0px 0px 10px
        }
    }
    .chat-body{
        @include fd;
        width: calc(100% - 30px);
        gap: 10px;
        height: calc(100% - 70px - 60px); // removed a - 100px. might need to include it later
        // max-height: 100%;
        overflow: scroll;
        .text{
            width: max-content;
            min-width: 30px;
            max-width: 240px;
            border-radius: 15px;
            display: flex;
            padding: 10px;
            gap: 15px;
        }
    }
    .empty-chat-body{
        @include fja;
        border-bottom: solid 1px rgb(40, 40, 40, .5);
        .display-container{
            @include fdja;
            gap: 20px;
            width: 350px;
            height: 350px;
            img{
                max-height: 70%;
                max-width: 100%;
                object-fit: cover;
            }
            p{
                text-align: center;
            }
        }
    }
    .chat-input-box{
        @include fa;
        justify-content: space-between;
        height: 70px;
        width: calc(100dvw - $side-menu-width - $chat-left-width - 10px);
        position: fixed;
    
        // position: absolute;
        bottom: 0px;
        img{
            width: 20px;
        }
        label{
            height: 50px;
            width: calc(100% - 60px)
        }
    }
}

// FAQ
.faq-table{
    @include fda;
    border: solid 1px rgb(45, 46, 46, .4);
    border-radius: 10px;
    width: calc(100% - 80px);
    margin-top: 20px;
    color: #282828;
    .faq-header{
        background-color: rgb(230, 230, 250, .3);
        border-radius: 6px;
        margin-top: 10px;
    }
    .row{
        width: calc(100% - 20px);
    }
    .column1,
    .column2,
    .column3{
        padding: 10px;
    }
    .column1{
        width: 15%;
    }
    .column2{
        width: 30%;
    }
    .column3{
        width: 55%;
    }
}


@media only screen and (max-width: 760px) {
    .top-navbar{
        justify-content: space-between;
