        padding: 0px;
    }
    // Components Dashboard Boxes
    .dashboard-boxes{
        grid-template-columns: auto;
    }
    // Components - Dashboard Tasks
    .dashboard-t-o-t-header.row{
        display: none;
    }
    .table{
        gap: 20px;
        .row{
            flex-direction: column;
            gap: 20px;
            height: max-content;
            border: solid 1px rgba(0, 24, 12, 0.2);
            border-radius: 5px;
            width: calc(100% - 33px);
            padding: 15px;
        }
    }

    
    // Pages
    .pages{
        overflow-x: hidden;
        .side-menu,
        .dashboard-ad-background
        // .page-right-header-b
        {
            display: none;
        }
        .page-right{
