    .dashboard-main-section{
        grid-template-areas:
        "dashboardbox dashboardbox"
        "dashboardbox dashboardbox"
        "dashboardcalendar dashboardcalendar"
        "classandexamschedule classandexamschedule"
        "dashboardtasks dashboardtasks"
        "dashboardgroups dashboardgroups";
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
        .dashboard-box{
            border-radius: 2px;
        }
        p{
            font-size: 1.05rem;
        }
        .dashboard-calendar{
            font-size: 1rem;
        }
    }
    // Pages - Landing Page
    .landing-page{
        h1, h2, h3, h4, h5, h6, p{
            text-align: center;
        }
    }
    .app-features{
        .heading{
            font-size: 1.5rem;
        }
    }

    // Pages - About-page
    .about-page{
        .header-text{
            font-size: 1.5rem;
        }
        p{
            font-size: 1rem
        }
    }
    .about-banner{
        @include fda;
        height: max-content;
        h1{
            font-size: 1.5rem;
        }
        .banner-left{
            p{
                font-size: 1rem;
            }
        }
