    .about-edvantage{
        grid-template-areas:
        "whoweareimage"
        "whowearedescrip"
        "whatdrivesusimage"
        "whatdrivesusdescrip";
        grid-template-columns: auto;
    }
    .key-features{
        margin-top: 20px;
        .features{
            grid-template-areas: 
            "feature1"
            "feature2"
            "feature3"
            "feature4";
        }
    }
    .work-with-us{
        .card{
            grid-template-areas:
            "workwithusimage"
            "workwithusdescrip";
            grid-template-columns: auto;
        }
