    .assignments-page{
        .table{
            width: 90%;
        }
        .assignment-header.row{
            display: none;
        }
    }
    // Pages - Chat Page
    .whole-chat-section{
        grid-template-columns: auto;
        height: calc(100dvh);
        border-top: 0px;
        .left{
            width: 100%;
            height: calc(100dvh - $page-right-header-height);
            .chat-profiles{
                border: 0px;
            }
        }
        .right{
            margin: 0px;
            height: 100%;
            .chat-input-box{
              width: calc(100dvw - 20px);
              gap: 10px;
            }
            .chat-header{
              gap: 10px;
              svg{
                  margin: 0px 0px 0px 20px;
                  display: block;
              }
            }
        }
        &.show-chat{
            .left{
                display: none;
            }
            .right {
              display: flex;
              height: calc(100dvh - $page-right-header-height);
              width: 100%;
              margin: 0px;
              .chat-input-box{
                width: calc(100dvw - 20px);
                gap: 10px;
              }
            }
        }
        .add-new-chat-profile{
            width: 80dvw;
        }
